export default {
	props: {
		name: String
	},
	data() {
		return {
			privateName: this.name + ''
		}
	},
	methods: {
		cancel() {
			this.$emit('toggle-input', false);
		},
		confirm() {
			this.$emit('input-confirm', this.privateName);
		},
		nameChange: function(e) {
			this.privateName = e.detail.value;
		}
	}
}
